import {debouce} from "./util.js"
import BackTop from 'components/content/BackTop/BackTop.vue';
export const itemListenerMixin ={
    mounted() {
        let refresh = debouce(this.$refs.scroll.refresh, 200);
        this.ItemEvent =() => {
          refresh();
        }
        this.$bus.$on("finishloading",this.ItemEvent);
    }
}

export const backTopMixin = {
  components:{
    BackTop
  },
  data(){
    return{
      isshow:false
    }
  },
  methods:{
    backTotop() {
      // 点击箭头使其滚动到开头
      //  console.log( this.$refs.scroll.scroll)
      this.$refs.scroll.scrollTo(0, 0, 1000); // 滚动到特定位置 0 0 代表位置 2000 是动画执行的时长 ms
    },
  }
}