<!--  -->
<template>
  <div class="swipper">
      <div id="box">
        <slot></slot>
      </div>
      <ul class="dot" v-show="disdot">
          <li @click="changePicture(index)" v-for="(item,index) in totalCount" :key="index" :class="{turnwhite:currentIndex == index}"></li>
      </ul>
  </div>
</template>

<script>
export default {
 name:'Swipper',
  data () {
    return {
      currentIndex:0,
      totalCount:[]
    };
  },
  methods: {    
     changePicture(index){
   
        this.currentIndex = index
        this.$children[this.currentIndex].isshow = false  
              for(let i=0;i<this.$children.length;i++){
                if(i!=this.currentIndex){
                  this.$children[i].isshow = true
                }
        }
      }
  },
  mounted() {
        this.totalCount = this.$children
         setInterval(()=>{
         
              this.$children[this.currentIndex].isshow = false  
              for(let i=0;i<this.$children.length;i++){
                if(i!=this.currentIndex){
                  this.$children[i].isshow = true
                }
              }
                   this.currentIndex++
              if(this.currentIndex==this.$children.length){
                this.currentIndex = 0
              }
          },1500)
  },
  props:{
    disdot:{
      type:Boolean,
      default:true
    }
  }
}
</script>
<style scoped>
.swipper{
    width:100%;
    height:180px;

    position: relative;
    overflow: hidden;
    
}
 .dot{
      position: absolute;
      top:85%;
      left:30%;
      height: 40px;
      list-style: none;
      color: white;
      z-index: 1;
      display: flex; 
  }
    .dot>li{
        display: flex;
        width: 8px;
        height:8px ;
        border-radius: 50%;
        border: 1px solid white;
        justify-content: center;
        margin-right:10px;
        align-items: center;
    }
    .turnwhite{
      background: white;
    }
</style>