<!--  -->
<template>
  <div class="good-item">
    <div>
      <!-- 这里的a标签最好这么写否则会将参数携带在路由跳转的地址后面 -->
      <a href="javascript:void(0)">
        <img
          :src="showImage"
          alt=""
          @load="loadingComplete()"
          @click="jump()"
        />
      </a>
    </div>

    <div class="good-content">
      <span class="over">{{list.title}}</span>
      <div class="includetwo">
        <span style="color: red">{{ list.price }}元</span>
        <span class="collect">{{ list.cfav }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {};
  },
  methods: {
    loadingComplete() {
      this.$bus.$emit("finishloading");
    },
    jump() {
      this.$router.push(`/detail/${this.list.iid}`);
    },
  },
  props: {
    list: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    showImage() {
      return this.list.image || this.list.show.img;
    },
  },
};
</script>
<style scoped>
.good-item {
  width: 45%;
  height: 300px;
  display: flex;
  flex-direction: column;
}
.good-item > div > a > img {
  width: 100%;
  height: 250px;
  display: block;
}
.good-content {
  width: 100%;
  font-size: 15px;
}
.over{
  /* 这里设置span标签为行内块级元素是为了设置宽度以便能在一定范围内限制文本大小 */
  display: inline-block;   
  width:100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space:nowrap;
}
.collect::before {
  content: "\2764";
  color: red;
  display: inline-block;
  margin-left: 10px;
}
.includetwo{
  text-align: center;
}
</style>