export function request(config){
    //1.创建axios的实例
    const instance  = axios.create({
        baseURL:"http://152.136.185.210:7878/api/m5",
        timeout:500
    })
    // 2. axios的请求拦截器
    // instance.interceptors.request.use(config =>{
    //     console.log(config)
    //     // 1. 比如confi中的一些信息不符合服务器的要求，需要做一些处理
    //     // 2. 比如每次发送网络请求时，都希望在界面显示一个请求图标
    //     // 3. 某些网络请求（比如登录(token)）,必须携带一些特殊信息      
    //     return config                   // 必须将配置信息返回，否则内部拿不到配置信息会报错
    // },err=>{
    //     console.log(err)
    // })
    // 3.axios的响应拦截器
    instance.interceptors.response.use(res=>{
        return res.data    //这里也必须返回结果
    },erro=>{
        console.log(erro)
    })
    // 3.发送真正的网络请求
     return instance(config)     //这个instance函数回返回一个Promise对象

}